main {
    margin: 0;
}
.preview, .landing-section {
    max-width: 90%;
    width: 90%;
    margin: auto;
}
.main {
    margin-bottom: 0;
}
/*landing section styling*/
.landing-section {
    display: flex;
    flex-direction: row;
    gap: 2em;
    justify-content: space-between;
    width: auto;
    margin: 0 auto;
    padding: 3em;
}
.metaverse h2 {
    font-weight: 400;
    font-size: 56px;
    line-height: 78.4px;
    color: #434343;
    margin-bottom: 48px;
    margin-top: 0;
}
.metaverse p {
    margin: 0;
    color: #434343;
    font-size: 24px;
}
.purple {
    color: #A02279;
    font-weight: 700;
}
.img-set1 {
    display: flex;
    gap: 0.5em;
}
.img-set1 img {
    width: 234px;
    height: 176px;
}
.flex-column {
    display: flex;
    gap: 0.5em;
    flex-direction: column;
}
.flex-column > * {
    width: 100%;
}
.flex-column1 {
    margin-top: 88px;
}
.search-bar {
    margin-top: 48px;
}
#search {
    max-width: 390px;
    background-color: #F7F7F7;
    border: 0.9px solid #A3A3A3;
    border-top-left-radius: 8px;
    border-bottom-left-radius: 8px;
    border-right: none;
    height: 54px;
    padding-left: 15px;
    padding-right: 226px;
}
#search-submit {
    padding: 17px 87px;
    background-color: #A02279;
    color: #fff;
    font-weight: 400;
    font-size: 16px;
    border: none;
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
    cursor: pointer;
}
/*sponsors section styling*/
.sponsors {
    margin: 2em 0;
    height: 70px;
    background-color: #A02279;
    color: #fff;
    display: flex;
    justify-content: space-evenly;
    padding: 0;
}
.sponsor-img {
    display: block;
    width: 222px;
    max-height: 50px;
    margin: 10px 0;
}
/*preview section styling*/
.item-container {
    width: 100%;
    max-width: 292px;
    border: 1px solid #D7D7D7;
    border-radius: 15px;
    padding: 16px;
    margin: 0;
}
.item {
    width: 100%;
    max-width: 260px;
    min-width: 200px;
    max-height: 265px;
}
.line1, .line2 {
    display: flex;
    justify-content: space-between;
}
.line1 > *, .line2 > * {
    width: 100%;
    font-size: 14px;
}
.line1 > * {
    margin-bottom: 0;
}
.price {
    font-weight: 700;
}
.star-reviews {
    display: flex;
    gap: 0.5em;
}
.preview, .preview-desktop, .preview-tablet {
    display: flex;
    gap: 1em;
}
.preview {
    flex-direction: column;
    gap: 1em;
    max-width: 100%;
    margin: 0;
}
.preview h1 {
    font-weight: 700;
    font-size: 48px;
    color: #000;
    text-align: center;
    margin: 0;
}
/*NFTs section styling*/
.nfts {
    background: #A02279;
    display: flex;
    justify-content: space-around;
    align-items: center;
    gap: 22em;
    padding: 8em;
}
.nft-info h1 {
    font-size: 48px;
    font-weight: 700;
    color: #fff;
    margin-top: 0;
}
.nft-ingo p {
    width: 415px;
}
.nft-info {
    font-size: 18px;
    font-weight: 400;
    color: #fff;
    max-width: 420px;
}
#learn-more {
    padding: 14px 33px;
    color: #A02279;
    background-color: #fff;
    font-weight: 400;
    font-size: 16px;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    margin-top: 58px;
}
.nft-img {
    max-width: 704px;
    max-height: 575px;
}

/*media queries*/
@media screen and (max-width: 1025px) {
    .landing-section {
        flex-direction: column;
    }
    .preview-tablet {
        flex-direction: column;
    }
}
@media screen and (max-width: 450px) {
    #search {
        width: 90%;
    }
    .item-container {
        max-width: 90%;
    }
}
@media screen and (max-width: 545px) {
    .img-set1 {
        flex-direction: column;
        gap: 0.5em;
    }
    .flex-column1 {
        margin-top: 0;
    }
}
@media screen and (max-width: 768px) {
    .landing-section {
        padding: 3em 2em;
    }
    #search {
        display: block;
        max-width: 60%;
        border-radius: 8px;
        border: 0.9px solid #A3A3A3;
        margin-bottom: 0.5em;
        padding: 16px;
        min-width: 200px;
    }
    #search-submit {
        border-radius: 8px;
    }
    .sponsors {
        flex-direction: column-reverse;
        height: fit-content;
        align-items: center;
        justify-content: center;
        text-align: center;
    }
}
@media screen and (max-width: 988px) {
    .nfts {
        flex-direction: column;
        gap: 4em;
        padding: 5em;
    }
    .nft-img {
        width: 100%;
        max-width: 420px;
        min-width: 250px;
    }
}