main, .places-to-stay {
    max-width: 100%;
    margin: 0 auto 92px auto;
}
.nav-menu-list {
    list-style-type: none;
    display: flex;
    justify-content: space-between;
    gap: 48px;
    padding: 0;
}
.list-items {
    font-size: 20px;
    font-weight: 400;
    color: #434343;
    padding: 14px 0;
}
.location {
    border: 1px solid #B4B4B4;
    border-radius: 8px;
    display: flex;
    gap: 1em;
    padding: 14px;
}
.location-span, .menu {
    display: block;
}
/**/
.item-container {
    width: 292px;
    max-width: 292px;
    border: 1px solid #D7D7D7;
    border-radius: 15px;
    padding: 16px;
    margin: 0.5em;
}
.item {
    max-width: 260px;
    max-height: 265px;
}
.line1, .line2 {
    display: flex;
    justify-content: space-between;
}
.line1 > *, .line2 > * {
    width: 100%;
    font-size: 14px;
}
.line1 > * {
    margin-bottom: 0;
}
.price {
    font-weight: 700;
}
.star-reviews {
    display: flex;
    gap: 0.5em;
}
.preview, .preview-tablet {
    display: flex;
    flex-direction: column;
    gap: 1em;
}
.preview-desktop {
    display: flex;
    gap: 1em;
    max-width: -moz-fit-content;
    max-width: fit-content;
    margin: 0 auto;
}
@media screen and (max-width: 1024px) {
    .nav-menu {
        padding-top: 0;
        padding-bottom: 0;
    }
    .list-items {
        display: none;
    }
    .location {
        display: unset;
        flex-direction: row;
        display: flex;
        margin: 0 0 0 1em;
    }
}
@media screen and (min-width: 1024px) {
    .preview-tablet {
        flex-direction: row;
    }
}
@media screen and (max-width: 988px) {
    .preview-tablet {
        flex-direction: column;
    }
}
@media (max-width: 768px) {
    .preview-desktop {
        flex-direction: column;
    }
}