footer {
    padding: 3em 7em;
    background-color: #1D1D1E;
    color: #fff;
}
.footer {
    display: flex;
    justify-content: space-between;
    margin-top: 3em;
    gap: 5em;
}
.footer > * {
    width: 100%;
    padding: 0;
}
.footer1 {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.socials {
    display: flex;
    gap: 2em;
}
.socials > * {
    width: 100%;
    max-width: 18px;
    max-height: 18px;
}
.footer-list {
    list-style-type: none;
    margin: 0;
}
.footer-list li {
    width: fit-content;
    margin-top: 1em;
}
.footer-list a {
    text-decoration: none;
    color: #F1F3F9;
    font-weight: 400;
    cursor: pointer;
}
.footer-list h4 {
    margin-top: 0;
    font-weight: 700;
    font-size: 18px;
}
.contact-us-footer, .learn-more-footer, .discord {
    margin: 0;
}
.copyright {
    margin-top: 3em;
    font-size: 16px;
    font-weight: 400;
    max-width: fit-content;
}

/*media queries*/
@media screen and (max-width: 768px) {
    .footer1 {
        justify-content: left;
        gap: 2em;
    }
}
@media screen and (min-width: 768px) and (max-width: 1025px) {
    .footer-list-flex {
        display: block;
        display: flex;
        justify-content: space-between;
    }
}
@media screen and (max-width: 988px) {
    footer {
        padding: 3em 2em;
    }
    .footer {
        flex-direction: column;
        justify-content: left;
        gap: 2em;
    }
}