@import url('https://fonts.googleapis.com/css2?family=Sora:wght@100;200;300;400;500;600;700;800&display=swap');

header {
    max-width: 90%;
    margin: auto;
}
.navbar {
    display: flex;
    justify-content: space-between;
    padding: 2em 0;
}
.home {
    display: flex;
    gap: 0.3em;
    margin-top: 0.2em;
}
.home > * {
    width: 100%;
}
.home-img {
    width: 42px;
    height: 36px;
}
.home-text {
    width: 158px;
    height: 32px;
    margin-top: 0.2em;
}
.list {
    display: flex;
    justify-content: space-between;
    gap: 48px;
    list-style-type: none;
    margin: 0;
    padding: 0;
}
.list li {
    margin-top: 0.5em;
}
.list a {
    text-decoration: none;
    color: #434343;
    font-weight: 400;
    font-size: 20px;
}
.header-button {
    display: block;
    border-radius: 10px;
    background-color: #A02279;
    color: #fff;
    font-weight: 400;
    font-size: 16px;
    border: none;
    cursor: pointer;
    padding: 14px 28px;
}
/*modal styling*/
body.active-modal {
    overflow-y: hidden;
}
.modal, .overlay {
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: fixed;
}
.overlay {
    background: rgba(49, 49, 49, 0.8);
}
.modal-content {
    position: absolute;
    top: 35%;
    left: 35%;
    background: #F1F1F1;
    padding: 14px 28px;
    border-radius: 16px;
    max-width: 600px;
    min-width: 300px;
    width: 450px;
}
.modal-head {
    display: flex;
    justify-content: space-between;
    margin: 0;
    padding-bottom: 12px;
}
.modal-head h2 {
    font-weight: 700;
    font-size: 24px;
    color: #333;
    line-height: 30px;
    margin: 0;
}
.close-modal {
    width: 24px;
    height: 24px;
    cursor: pointer;
}
.wallet {
    display: flex;
    justify-content: space-between;
    border: 1px solid #CFD8DC;
    width: 400px;
    max-width: 580px;
    padding: 12px 20px;
    border-radius: 12px;
    cursor: pointer;
}
.wallet:hover {
    background: #DDE;
}
.walletconnect {
    margin-bottom: 1em;
}
.wallet-flex {
    display: flex;
    gap: 16px;
}
.wallet-logo {
    width: 44px;
    height: 44px;
}
.wallet-flex h3 {
    font-weight: 600;
    font-weight: 'Sora', serif;
    margin: 0.6em 0 0 0;
}
.go-to {
    display: block;
    width: 16px;
    height: 12px;
    margin-top: 1.1em;
}
hr {
    width: 100%;
    display: block;
}

/**/
@media screen and (max-width: 988px) {
    .header-list-item {
        display: none;
    }
    #places {
        display: unset;
        border: 1px solid #CFD8DC;
        padding: 8px;
        border-radius: 8px;
    }
}
@media screen and (max-width: 768px) {
    .navbar {
        justify-content: space-between;
    }
    .connect-btn {
        display: none;
    }
}
@media screen and (max-width: 450px) {
    .home-text {
        display: none;
    }
}